import React from 'react';
import './index.scss'; // Import for SCSS styles
import { Routes, Route, Navigate } from "react-router-dom";
import { AboutUs, Landing, NotFound, Vision, Mission, Gallery, Contact } from './pages';
// If you want you can use SCSS instead of css
import 'lightgallery/scss/lightgallery.scss';
import 'lightgallery/scss/lg-zoom.scss';

function App() {
  return (
    <Routes>
      <Route path="/indiana" element={<Landing />} />
      <Route path="indiana/about" element={<AboutUs />} />
      <Route path="indiana/vision" element={<Vision />} />
      <Route path="indiana/gallery" element={<Gallery />} />
      <Route path="indiana/mission" element={<Mission />} />
      <Route path="not-found" element={<NotFound />} />
      <Route path="indiana/contact" element={<Contact />} />
      <Route path="*" element={<Navigate to="/not-found" replace />} />
    </Routes>
  );
}

export default App;
